body {
  background-image: url(https://i.pinimg.com/564x/92/6b/c8/926bc8f7afd226a935de1ee3390e740b.jpg);
  color: rgb(243, 243, 224);
  font-family: serif;
  font-size: larger;
  font-weight: 500;
}

.selected {
background: rgb(19, 62, 135);
}

.options {
padding: 40px;
cursor: pointer;
border: 2px solid rgb(19, 62, 135);
font-size: larger;
}

ul {
list-style-type: none;
font-size: larger;
}

.boarding {
border: 1px solid rgb(243, 243, 224);
border-radius: 12px;
max-width: 4000px;
}

.btn-color {
background-color: rgb(19, 62, 135);
color: rgb(243, 243, 224);
border: 3px solid rgb(243, 243, 224);
width: 300px;
height: 90px;
border-radius: 12px;
margin-top: 10px;
}